import ContentControls from "./content-control-common";
import {chrome} from "../../../utils/polyfill.ts";
import { 
    allResourceTypes, 
    DYNAMIC_RULE_CONTENT_CONTROL_PRIORITY, 
    getLastDynamicRuleId 
} from "../mv3/dynamic-rule-utils";

export class Mv3ContentControls extends ContentControls {
    constructor() {
        super();
        this.getAll().then((cache) => {
            this.CACHE = cache;
        });
    }

    async export() {
        return this.getAll();
    }

    async import(contentControlList) {
        // clear all existing items
        await this.deleteAllItems();

        // add all items from the list
        for(let i = 0; i < contentControlList.length; i++) {
            await this.addItem(
                contentControlList[i][0], // url
                contentControlList[i][1] // category
            );
        }
    }

    async addItem(url, category = 'general') {
        console.debug('CCTRL MV3: Adding item to cache', {url, category});
        if (!this.CACHE) {
            this.CACHE = new Map();
        }
        
        let newRules = [];
        let lastRuleId = await getLastDynamicRuleId();
        const newRuleId = lastRuleId + 1;
        const newRule = {
            id: newRuleId,
            priority: DYNAMIC_RULE_CONTENT_CONTROL_PRIORITY,
            action: {
                type: 'redirect',
                redirect: {
                    extensionPath: `/app/eventpages/block-mv3.html?type=user-block&url=${url}`,
                },
            },
            condition: {
                urlFilter: url,
                resourceTypes: allResourceTypes,
            }
        };
        newRules.push(newRule);
        
        try {
            await chrome.declarativeNetRequest.updateDynamicRules({
                addRules: newRules,
                removeRuleIds: []
            });
            let error = chrome.runtime.lastError;
            if (error) {
                console.error('Error adding dynamic rules', error);
            }
            const categories = this.CACHE.has(url) ? new Set(this.CACHE.get(url)) : new Set();
            categories.add(category);
            this.CACHE.set(url, categories);
            console.debug('CCTRL: Added item to cache', {url, category, cache: this.CACHE});
        } catch (err) {
            console.error('CCTRL: Error adding item to cache', err);
            throw err;
        }
    }

    async deleteItem(url, _category) {
        let rules = await chrome.declarativeNetRequest.getDynamicRules();
        let ruleIdToBeDeleted = rules.find((rule) => {return rule.condition.urlFilter === url})?.id;
        // check if ruleIdToBeDeleted is undefined
        if (ruleIdToBeDeleted === undefined) {
            console.error('Error deleting item: ruleIdToBeDeleted is undefined');
            return false;
        }
        await chrome.declarativeNetRequest.updateDynamicRules({
            addRules: [],
            removeRuleIds: [ruleIdToBeDeleted]
        }, () => {
            let error = chrome.runtime.lastError;
            if (error) {
                console.error('Error removing session rules', error);
            }
        });
        if (this.CACHE) {
            this.CACHE.delete(url);
            console.debug('CCTRL: Deleted item from cache', {url, cache: this.CACHE});
            return true;
        }
        return false;
    }

    async deleteAllItems() {
        let rules = await chrome.declarativeNetRequest.getDynamicRules();
        let ruleIds = rules.map(({id}) => id);
        await chrome.declarativeNetRequest.updateDynamicRules({
            addRules: [],
            removeRuleIds: ruleIds
        }, () => {
            let error = chrome.runtime.lastError;
            if (error) {
                console.error('Error removing session rules', error);
            }
        });
        if (this.CACHE) {
            this.CACHE.clear();
            return true;
        }
        return false;
    }

    existsForUrl(url) {
        return this.CACHE.has(url);
    }

    async getAll() {
        let rules = await chrome.declarativeNetRequest.getDynamicRules();
        const filteredRules = rules.filter((rule) => {
            return rule.priority === DYNAMIC_RULE_CONTENT_CONTROL_PRIORITY;
        })
        console.debug('Mv3ContentControls.getAll - rules: ', filteredRules);
        if (!this.CACHE) {
            this.CACHE = new Map();
        }
        for (let [key, value] of filteredRules.map((rule) => [rule.condition.urlFilter, 'general'])) {
            this.CACHE.set(key, value);
        }
        return this.CACHE;
    }

    getForUrl(_url) {
        let rules = this.getAll();
        return rules.find((rule) => rule.condition.urlFilter === _url);
    }

    saveItems() {
        // dynamic rules are saved automatically
    }

    loadSavedItems() {
        // dynamic rules are loaded automatically
    }
}
